.form{
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 17px;
}

main{
    padding: 36px;
}

.form-input{
    font-family: "Karla" ,sans-serif;
    border-radius: 5px;
    border: 1px solid #D5D4D8;
}

.form-button{
    grid-column: 1/-1;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    color: white;
    border: none;
    cursor: pointer;
}

.meme{
    margin: 10px 2.5%;
    width: 95%;
    position: relative;
    display: flex;
    justify-content:center ;
}

.meme-image{
    width: 100%;
}

.meme-topText{
    z-index: 1;
    color: white;
    text-transform: uppercase;
    text-shadow: 2px 2px black;
    font-size: 50px;
    position: absolute;
    top: 0;
    
}

.meme-bottomText{
    z-index: 1;
    color: white;
    text-transform: uppercase;
    text-shadow: 2px 2px black;
    font-size: 50px;
    position: absolute;
    bottom: 0;
}

