*{
    box-sizing: border-box;
}

.header-box{
    display: flex;
    align-items: center;
    height: 65px;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    padding: 15px 20px;
}

img{
    height: 100%;
    margin-right: 6px;
}

h1{
    font-size: 20px;
    margin-right: auto;    
}

p{
    font-size: 12px;
    font-weight: 500;
}